html {
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

article, aside, footer, header, nav, section {
  display: block; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

figcaption, figure, main {
  display: block; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace, monospace;
  font-size: 1em; }

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

b, strong {
  font-weight: inherit; }

b, strong {
  font-weight: bolder; }

code, kbd, samp {
  font-family: monospace, monospace;
  font-size: 1em; }

dfn {
  font-style: italic; }

mark {
  background-color: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

audio, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

img {
  border-style: none; }

svg:not(:root) {
  overflow: hidden; }

button, input, optgroup, select, textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0; }

button, input {
  overflow: visible; }

button, select {
  text-transform: none; }

button, html [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button; }

button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring, [type="button"]:-moz-focusring, [type="reset"]:-moz-focusring, [type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  padding: 0.35em 0.75em 0.625em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

progress {
  display: inline-block;
  vertical-align: baseline; }

textarea {
  overflow: auto; }

[type="checkbox"], [type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-cancel-button, [type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

details, menu {
  display: block; }

summary {
  display: list-item; }

canvas {
  display: inline-block; }

template {
  display: none; }

[hidden] {
  display: none; }

* {
  box-sizing: border-box; }

body {
  background: #fff;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 18px; }

h1 {
  font-size: 1.4rem;
  text-align: left;
  display: block;
  margin: 0 0.5rem;
  padding: 0.5rem 0.2rem 0.2rem 0.2rem;
  user-select: none; }

button {
  background: #F8F9FA;
  border: 1px solid #ccc;
  border-radius: 0.5rem; }
  button:hover {
    background-color: #dae0e5; }
  button:active {
    background-color: #bdc6d0; }
  button.start-finish {
    background-color: #007BFF;
    border: none;
    color: #fff; }
    button.start-finish:hover {
      background-color: #3395ff; }
    button.start-finish:active {
      background-color: #66b0ff; }
    button.start-finish[value=finish] {
      background-color: #28A745; }
      button.start-finish[value=finish]:hover {
        background-color: #34ce57; }
      button.start-finish[value=finish]:active {
        background-color: #5dd879; }

.wrapper {
  position: relative;
  margin: 0 auto;
  height: 100vh;
  min-width: 280px;
  width: 100%;
  max-width: 768px; }

#log {
  list-style: none;
  position: absolute;
  top: 5rem;
  right: 0.2rem;
  bottom: 6.6rem;
  left: 0.2rem;
  margin: 0;
  padding: 0.5rem;
  overflow-y: scroll; }
  #log li.start, #log li.finish, #log li.auto {
    color: #28a745; }
  #log li.penalty {
    color: #ffc107; }
  #log li.hidden {
    visibility: hidden; }
  #log li.limit {
    color: #DC3545; }
  #log li.pause, #log li.resume {
    color: #6f42c1; }
  #log li .time {
    color: #aaa;
    font-size: 0.8rem; }

#btnSettings {
  position: absolute;
  top: 0.4rem;
  right: 0.4rem; }

#pnlSettings {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 0.5rem;
  padding: 0.2rem;
  position: absolute;
  top: 2.2rem;
  right: 0.2rem;
  bottom: 6.6rem;
  left: 0.2rem;
  user-select: none;
  z-index: 2; }
  #pnlSettings h2 {
    font-size: 1rem;
    margin: 0.1rem;
    text-align: center;
    user-select: none; }
  #pnlSettings label {
    display: block;
    position: relative;
    height: 1.8rem; }
  #pnlSettings select {
    float: right;
    width: 4em; }

#pnlTime {
  display: flex;
  color: #aaa;
  font-size: 0.8rem;
  justify-content: space-around;
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center; }
  #pnlTime li {
    margin: 0;
    padding: 0 0.3rem; }
  #pnlTime span {
    color: #000;
    font-size: 1.2rem;
    font-weight: bold; }

.controls {
  position: absolute;
  bottom: 0.2rem;
  left: 0.2rem;
  right: 0.2rem;
  user-select: none; }
  .controls > div {
    display: flex;
    justify-content: center; }
  .controls button {
    flex-basis: 20%;
    height: 3rem;
    margin: 0 0.1rem; }
    .controls button:first-child {
      margin: 0 0.1rem 0 0; }
    .controls button:last-child {
      margin: 0 0 0 0.1rem; }
  .controls #btnStartFinish {
    width: 100%;
    height: 3rem;
    margin-bottom: 0.2rem; }
  .controls #btnNextSector,
  .controls #btnPause {
    flex-basis: 30%; }
