@import "_normalize.scss";

* {
  box-sizing: border-box;
}

body {
  background: #fff;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 18px;
}

h1 {
  font-size: 1.4rem;
  text-align: left;
  display: block;
  margin: 0 0.5rem;
  padding: 0.5rem 0.2rem 0.2rem 0.2rem;
  user-select: none;
}

button {
  background: #F8F9FA;
  border: 1px solid #ccc;
  border-radius: 0.5rem;

  &:hover {
    background-color: darken(#F8F9FA, 10%);
  }

  &:active {
    background-color: darken(#F8F9FA, 20%);
  }

  &.start-finish {
    background-color: #007BFF;
    border: none;
    color: #fff;
    
    &:hover {
      background-color: lighten(#007BFF, 10%);
    }

    &:active {
      background-color: lighten(#007BFF, 20%);
    }

    &[value=finish] {
      background-color: #28A745;

      &:hover {
        background-color: lighten(#28A745, 10%);
      }

      &:active {
        background-color: lighten(#28A745, 20%);
      }
    }
  }
}

.wrapper {
  position: relative;
  margin: 0 auto;
  height: 100vh;
  min-width: 280px;
  width: 100%;
  max-width: 768px;
}

#log {
  list-style: none;
  position: absolute;
  top: 5rem;
  right: 0.2rem;
  bottom: 6.6rem;
  left: 0.2rem;
  margin: 0;
  padding: 0.5rem;
  overflow-y: scroll;

  li {
    &.start,
    &.finish,
    &.auto {
      color: #28a745;
    }

    &.penalty {
      color: #ffc107;
    }

    &.hidden {
      visibility: hidden;
    }

    &.limit, {
      color: #DC3545;
    }

    &.pause,
    &.resume {
      color: #6f42c1;
    }

    .time {
      color: #aaa;
      font-size: 0.8rem;
    }
  }
}

#btnSettings {
  position: absolute;
  top: 0.4rem;
  right: 0.4rem;
}

#pnlSettings {
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 0.5rem;
  padding: 0.2rem;
  position: absolute;
  top: 2.2rem;
  right: 0.2rem;
  bottom: 6.6rem;
  left: 0.2rem;
  user-select: none;
  z-index: 2;

  h2 {
    font-size: 1rem;
    margin: 0.1rem;
    text-align: center;
    user-select: none;
  }

  label {
    display: block;
    position: relative;
    height: 1.8rem;
  }

  select {
    float: right;
    width: 4em;
  }
}

#pnlTime {
  display: flex;
  color: #aaa;
  font-size: 0.8rem;
  justify-content: space-around;
  list-style: none; 
  margin: 0;
  padding: 0;
  text-align: center;

  li {
    margin: 0;
    padding: 0 0.3rem;
  }

  span {
    color: #000;
    font-size: 1.2rem;
    font-weight: bold;
  }
}

.controls {
  position: absolute;
  bottom: 0.2rem;
  left: 0.2rem;
  right: 0.2rem;
  user-select: none;

  & > div {
    display: flex;
    justify-content: center;
  }

  button {
    flex-basis: 20%;
    height: 3rem;
    margin: 0 0.1rem;
  
    &:first-child {
      margin: 0 0.1rem 0 0;
    }

    &:last-child {
      margin: 0 0 0 0.1rem;
    }
  }

  #btnStartFinish {
    width: 100%;
    height: 3rem;
    margin-bottom: 0.2rem;
  }
  
  #btnNextSector,
  #btnPause {
    flex-basis: 30%;
  }
}

